

















import {Component, Emit, Prop, Vue} from 'vue-property-decorator';

@Component
export default class HexButton extends Vue {
  @Prop() private label!: string;
  @Prop() private active!: boolean;
  animation = false;

  @Emit('click')
  onClick() {
      this.animation = true;
      setTimeout(() => this.animation = false, 500);
  }
}
